<template>
    <div id="StoryBlock">
        <div class="container" id="StoryCards">
            <div class="row mx-3">
                <div class="col-md-4 my-3" v-for="(item, index) in Events" :key="index">
                    <div class="card text-center">
                        <div class="card-header">
                            <b>{{ item["EventName"][language] }}</b>
                        </div>
                        <div class="card-body">
                            <a href="#" @click="ShowStoryChapter(item)"><img :src="`img/EventStory/${item['img']}`" class="w-50" alt=""></a>
                        </div>
                        <div class="card-footer text-muted">
                            {{ item["startTime"] }} ~ {{ item["endTime"] }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="container" id="StoryChapters">
            <div class="row mx-3">
                <ul class="list-group my-3 rounded rounded-4">
                    <li class="list-group-item"><a href="#" @click="ShowStoryCards"><i class="bi bi-arrow-left fs-4"></i></a></li>
                    <li class="list-group-item" v-for="(item, index) in event['Storys']" :key="index">
                        <a href="#" @click="ShowStoryContent(item)">
                            <img class="StoryBook" src="img/UI_Icon_Stage_Book1.png" alt="">
                            {{ event["EventName"][language] }} {{ index + 1 }}
                        </a>
                    </li>
                </ul>
            </div>
        </div>
        <div class="container" id="StoryContent">
            <div class="row mx-3">
                <ul class="list-group my-3 rounded rounded-4">
                    <li class="list-group-item d-flex">
                        <a class="me-auto" href="#" @click="HideStoryChapter">
                            <i class="bi bi-arrow-left fs-4"></i>
                        </a>
                        <a href="#" @click="PrevDialogue">
                            <i class="bi bi-caret-left-fill fs-4 px-3"></i>
                        </a>
                        <a href="#" @click="NextDialogue">
                            <i class="bi bi-caret-right-fill fs-4 px-3"></i>
                        </a>
                        <!-- <a href="#" @click="toggleMusic">
                            <i class="bi bi-music-note-beamed fs-4 px-3"></i>
                        </a>
                        <a href="#" @click="toggleText">
                            <i class="bi bi-chat-left-dots fs-4 px-3"></i>
                        </a> -->
                    </li>
                    <li class="list-group-item" @click="NextDialogue">
                        <div id="dialogueBlock" class="position-relative unselectable" :style="{ 'background-image': 'url(img/backgrounds/' + dialogueBackground + '.png)' }">
                            <div class="position-absolute top-85 start-50 translate-middle bg-dark" style="width:80%; height:27%; opacity: 0.5;"></div>
                            <div id="dialogueCharacter" class="position-absolute top-80 start-50 translate-middle d-flex" v-if="dialogueCharacter.length">
                                <img class="nameBorder" src="img/UI_Icon_VisualNovel_NameBorder.png" alt="">
                                <h4 class="text-light text-outline" v-html="dialogueCharacter"></h4>
                                <img class="nameBorder flipX" src="img/UI_Icon_VisualNovel_NameBorder.png" alt="">
                            </div>
                            <p id="dialogueText" class="position-absolute top-90 start-50 translate-middle fs-5 text-light text-outline" v-html="dialogueText"></p>
                        </div>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
import { ref, watch } from 'vue'
import { useMobile } from "@/store/Mobile"
import { useLanguage } from '@/store/Language'
import { storeToRefs } from 'pinia'
import axios from 'axios'
import Events from '@/assets/json/Events.json'

export default {
    name: 'EventStory',
    setup() {
        const { isMobile } = storeToRefs(useMobile())
        const { language, text } = storeToRefs(useLanguage())
        const event = ref({})
        const dialogue = ref([])
        const dialogueIndex = ref(0)
        const dialogueCharacter = ref('')
        const dialogueText = ref('')
        const dialogueMusic = ref('')
        const dialogueBackground = ref('feywood')
        const outerLeft = ref('')
        const innerLeft = ref('')
        const center = ref('')
        const innerRight = ref('')
        const outerRight = ref('')
        const ShowStoryChapter = (item) => {
            event.value = item
            $('#StoryChapters').animate({"left":"0"}, "slow").addClass('visible');
            $('#StoryCards').animate({"left":"-100%"}, "slow").removeClass('visible');
        }
        const ShowStoryCards = () => {
            $('#StoryChapters').animate({"left":"100%"}, "slow").removeClass('visible');
            $('#StoryCards').animate({"left":"0%"}, "slow").addClass('visible');
        }
        const ShowStoryContent = (item) => {
            axios.get(`json/EventStory/${item}`)
                .then((res)=>{
                    dialogueIndex.value = 0
                    dialogue.value = res.data.dialogueDataEntries[language.value].dialogueLines
                    changeDialogue()
                })
            $('#StoryContent').animate({"left":"0"}, "slow").addClass('visible');
            $('#StoryChapters').animate({"left":"-100%"}, "slow").removeClass('visible');
        }
        const HideStoryChapter = () => {
            $('#StoryContent').animate({"left":"100%"}, "slow").removeClass('visible');
            $('#StoryChapters').animate({"left":"0%"}, "slow").addClass('visible');
        }
        const PrevDialogue = () => {
            if (dialogueIndex.value > 0){
                dialogueIndex.value -= 1
                changeDialogue()
            }
        }
        const NextDialogue = () => {
            if (dialogueIndex.value < dialogue.value.length - 1){
                dialogueIndex.value += 1
                changeDialogue()
            }
        }
        const changeDialogue = () => {
            dialogueCharacter.value = dialogue.value[dialogueIndex.value]["characterID"]
            dialogueText.value = dialogue.value[dialogueIndex.value]["dialogueText"]
            dialogueMusic.value = dialogue.value[dialogueIndex.value]["musicID"]
            dialogueBackground.value = dialogue.value[dialogueIndex.value]["backgroundID"] == '' ? dialogueBackground.value : dialogue.value[dialogueIndex.value]["backgroundID"]
            outerLeft.value = dialogue.value[dialogueIndex.value]["outerLeft"]
            innerLeft.value = dialogue.value[dialogueIndex.value]["innerLeft"]
            center.value = dialogue.value[dialogueIndex.value]["center"]
            innerRight.value = dialogue.value[dialogueIndex.value]["innerRight"]
            outerRight.value = dialogue.value[dialogueIndex.value]["outerRight"]
        }
        return {
            isMobile,
            text,
            language,
            Events,
            event,
            dialogue,
            dialogueIndex,
            dialogueCharacter,
            dialogueText,
            dialogueMusic,
            dialogueBackground,
            outerLeft,
            innerLeft,
            center,
            innerRight,
            outerRight,
            ShowStoryChapter,
            ShowStoryCards,
            ShowStoryContent,
            HideStoryChapter,
            NextDialogue,
            PrevDialogue
        }
    }
}
</script>

<style scoped>
#StoryBlock{
  height: 100vh;
  position: relative;
  overflow-x: hidden;
  overflow-y: visible;
}

#StoryCards {
    width: 100%;
    position:absolute;
}
#StoryChapters {
    width:100%;
    position:absolute;
    left:100%;
}
#StoryContent {
    width:100%;
    position:absolute;
    left:100%;
}
.StoryBook {
    width: 3em;
}
#dialogueBlock {
    height: 70vh;
    background-size: cover;
}
.top-80 {
    top: 80%!important;
}
.top-85 {
    top: 85%!important;
}
.top-90 {
    top: 90%!important;
}
.nameBorder {
    height: 50%;
}
.flipX {
    transform: rotateY(180deg);
}
.text-outline {
    text-shadow: 1.5px 0 0 #000, 0 -1.5px 0 #000, 0 1.5px 0 #000, -1.5px 0 0 #000;
}
.unselectable {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
</style>
